import React from 'react'
import { Channel, useChatContext, MessageTeam } from 'stream-chat-react'

import { ChannelInner, CreateChannel, EditChannel } from './'
//this class is responsible for the chat interface and will pop differentt windows for creating and editing channels and when theres
//  no chat it will inform the user to send something
const ChannelContainer = (
  {
    isCreating,
    setIsCreating,
    isEditing,
    setIsEditing,
    createType

  }

) => {
const {channel} = useChatContext();

if(isCreating){
return(
  <div className='channel__container'>
    <CreateChannel createType={createType} setIsCreating={setIsCreating}/>

  </div>

)

}

if(isEditing){
  return(
    <div className='channel__container'>
    <EditChannel setIsEditing={setIsEditing} />

  </div>

  )

}

const EmptyState = () => {
return(
    <div className='channel-empty__container'>
    <p className='channel-empty__first'>
      This is the beginning of your chat history.

    </p>

    <p className='channel-empty__second'>
      Send messagess, attachments, links, emojis, and more!
      
    </p>

  </div>
)

}

  return (
    <div className='channel__container'>
      <Channel EmptyStateIndicator={EmptyState} 
       channel={channel}
        Message={(messageProps, i) => <MessageTeam key={i} {...messageProps} />}>
        <ChannelInner setIsEditing={setIsEditing}/>

      </Channel>

    </div>
  )
}

export default ChannelContainer

